import React, { useEffect, useState } from "react";
import { Innstance } from "../../Axios";
import { USER } from "../../models/authentication.model";
import { Col, Form, Input, Row } from "antd";
import { LoadingOutlined } from "@ant-design/icons";

const Profile: React.FC = () => {
  const [user, setUser] = useState<USER>();
  useEffect(() => {
    Innstance.get("admin/me")
      .then((response) => {
        console.log(response);
        setUser(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);
  return (
    <div className="profile">
      <div className="profile__card">
        <main className="flex">
          <img
            src={require("../../assets/img/profile_img.jpeg")}
            alt=""
            title=""
          />
          <h1>{user?.name}</h1>
        </main>
      </div>
      <Form className="profile__form" disabled={true} layout="vertical">
        <Row gutter={[20, 20]}>
          <Col lg={12}>
            <Form.Item label="User Name">
              <Input
                type="text"
                size="large"
                placeholder="user name"
                value={user?.name}
              />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="Email">
              <Input
                type="text"
                size="large"
                placeholder="email"
                value={user?.email}
              />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="password">
              <Input type="text" size="large" placeholder="password" />
            </Form.Item>
          </Col>
          <Col lg={12}>
            <Form.Item label="Job">
              <Input type="text" size="large" placeholder="Job" />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default Profile;
